:root {
  --border: 3px solid var(--light-blue);
}

.activity-request {
  display: flex;
  flex-direction: column;
  font-family: var(--open-sans);
  padding: 30px;
}

.activity-request__header {
  color: var(--medium-blue);
  align-self: flex-start;
  font-size: 30px;
  font-weight: 600;
  margin-top: 0;
  text-transform: uppercase;
}

.activity-request__content {
  display: flex;
  align-items: flex-start;
}

.activity-request__back-link {
  border: var(--border);
  border-right: none;
  background-color: #fff;
  padding: 5px 20px 5px 5px;
  border-radius: 20px 0 0 20px;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 2;
}

.activity-request__back-link path {
  transition: fill 0.2s linear;
}

.activity-request__back-link:hover path {
  fill: #f00;
}

.activity-request__form {
  border: var(--border);
  margin-left: -3px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  flex-grow: 1;
  padding: 15px;
  position: relative;
}

.activity-request__form:before {
  content: "";
  position: absolute;
  display: block;
  --width: 70px;
  --height: calc(var(--width) * 0.649);
  width: var(--width);
  height: var(--height);
  background-image: url("./confetti.webp");
  top: 13px;
  right: 13px;
  background-repeat: no-repeat;
  background-size: contain;
}

.activity-request__caption {
  color: var(--medium-blue);
  margin: 20px 0;
  text-indent: 20px;
  font-weight: 600;
}

.activity-request__shorter-input {
  width: 80%;
}

.activity-request__participators-container,
.activity-request__attendants-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.activity-request__participators-list,
.activity-request__attendants-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.activity-request__participator-item {
  display: flex;
  gap: 10px;
}

.activity-request__participator-name,
.activity-request__participator-restrictions {
  flex-grow: 1;
}

.activity-request__participator-birth-date,
.activity-request__participator-age {
  flex-grow: 0.5;
}

.activity-request__participator-age {
  border-radius: 20px;
  border: 1px solid var(--light-blue);
  color: var(--medium-blue);
  font-size: 14px;
  padding: 12px 9px;
  background-color: #fff;
}

.activity-request__button-add {
  --diameter: 30px;
  width: var(--diameter);
  height: var(--diameter);
  align-self: flex-start;
  border-radius: 50%;
  background-color: var(--light-blue);
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  cursor: pointer;
  font-family: var(--montserrat);
  font-weight: 600;
  font-size: 28px;
  transition: background-color 0.2s linear;
}

.activity-request__button-add:hover {
  background-color: var(--medium-blue);
}

.activity-request__squad-agent-container {
  display: flex;
  gap: 10px;
}

.activity-request__squad-agent-name {
  flex-grow: 1;
}

.activity-request__squad-agent-phone {
  flex-grow: 0.5;
}

.activity-request__button-submit {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: flex-end;
  border-radius: 10px;
  border: none;
  text-transform: uppercase;
  color: #fff;
  background-color: #f00;
  height: 40px;
  font-family: var(--open-sans);
  font-weight: 600;
  font-size: 18px;
  cursor: pointer;
  width: 25%;
  transition: background-color 0.2s linear;
}

.activity-request__button-submit:hover {
  background-color: var(--medium-blue);
}

@media (max-width: 960px) {
  .activity-request {
    padding: 30px 5px;
  }

  .activity-request__header {
    align-self: center;
  }

  .activity-request__content {
    align-items: center;
    flex-direction: column;
  }

  .activity-request__back-link {
    border-radius: 20px 20px 0 0;
    border: var(--border);
    border-bottom: none;
  }

  .activity-request__form {
    margin-left: 0;
    width: 90%;
  }

  .activity-request__caption {
    text-indent: 0;
    align-self: center;
  }

  .activity-request__shorter-input {
    width: auto;
  }

  .activity-request__participators-list {
    gap: 25px;
  }

  .activity-request__participator-item {
    flex-direction: column;
    background-color: var(--light-grey);
    padding: 10px;
    border-radius: 20px;
  }

  .activity-request__squad-agent-container {
    flex-direction: column;
  }

  .activity-request__button-submit {
    width: 150px;
    align-self: center;
  }
}
